<template>
  <div class="min-h-screen bg-[#2C2F37] text-white">
    <nav class="fixed w-full bg-black/30 backdrop-blur-md z-50">
      <div class="container mx-auto px-4 py-4 flex justify-between items-center">
        <div class="text-2xl font-bold bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
          Portofolio
        </div>
        <div class="md:hidden" @click="toggleMenu">
          <div :class="['hamburger', { 'open': isMenuOpen }]">
            <div class="bar"></div>
            <div class="bar"></div>
            <div class="bar"></div>
          </div>
        </div>
        <ul class="md:flex space-x-6">
          <li v-for="item in menuItems" :key="item" class="hidden md:block">
            <a :href="'#' + item.toLowerCase().replace(' ', '')"
              @click.prevent="scrollToElement(item.toLowerCase().replace(' ', ''))"
              class="hover:text-secondary transition-colors duration-300">
              {{ item }}
            </a>
          </li>
        </ul>
        <ul v-if="isMenuOpen" class="md:hidden absolute top-16 right-0 bg-black/50 w-full px-4 py-6">
          <li v-for="item in menuItems" :key="item">
            <a :href="'#' + item.toLowerCase().replace(' ', '')"
              @click.prevent="scrollToElement(item.toLowerCase().replace(' ', ''))"
              class="hover:text-secondary transition-colors duration-300">
              {{ item }}
            </a>
          </li>
        </ul>
      </div>
    </nav>

    <div class="pt-20 space-y-24">
      <section id="biodata" class="min-h-screen flex items-center justify-center slide-in">
        <div class="container mx-auto px-4 text-center">
          <div class="relative w-48 h-48 mx-auto mb-8">
            <div class="absolute inset-0 bg-gradient-to-r from-primary to-secondary rounded-full animate-pulse"></div>
          </div>
          <h1 class="text-5xl font-bold mb-4 bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
            Zulfiqar Silmy Setiawan
          </h1>
          <p class="text-xl text-gray-300 max-w-2xl mx-auto">
            Software Engineer, UI/UX Designer, and Front-End Developer
          </p>
        </div>
      </section>

      <section id="education" class="py-20 slide-in">
        <div class="container mx-auto px-4">
          <h2
            class="text-4xl font-bold text-center mb-12 bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
            Educational Background
          </h2>
          <div class="max-w-3xl mx-auto">
            <div v-for="(edu, index) in education" :key="index" class="relative pl-8 pb-8 group">
              <div
                class="absolute left-0 top-0 w-1 h-full bg-gray-700 group-hover:bg-secondary transition-colors duration-300">
              </div>
              <div class="absolute left-[-5px] top-0 w-3 h-3 rounded-full margin bg-secondary"></div>
              <div
                class="bg-gray-800/50 p-6 rounded-lg backdrop-blur-sm hover:transform hover:translate-x-2 transition-all duration-300">
                <h3 class="text-xl font-bold text-primary">{{ edu.year }}</h3>
                <h4 class="text-lg font-semibold">{{ edu.school }}</h4>
                <p class="text-gray-400">{{ edu.description }}</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section id="techstack" class="py-20 bg-gray-900/50 slide-in">
        <div class="container mx-auto px-4">
          <h2
            class="text-4xl font-bold text-center mb-12 bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
            Tech Stack
          </h2>
          <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <div v-for="(tech, index) in techStacks" :key="index" class="flex flex-col items-center space-y-4">
              <img :src="tech.image" alt="Tech Stack Image" class="w-20 h-20 object-cover">
              <span class="text-lg font-semibold">{{ tech.name }}</span>
            </div>
          </div>
        </div>
      </section>


      <section id="certificate" class="py-20 slide-in">
        <div class="container mx-auto px-4">
          <h2
            class="text-4xl font-bold text-center mb-12 bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
            Certificate
          </h2>
          <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <div v-for="(item, index) in certificate" :key="index"
              class="group relative overflow-hidden rounded-xl shadow-lg">
              <div class="aspect-video bg-gray-800 flex items-center justify-center">
                <img :src="item.image" alt="Certificate" class="w-full h-full object-cover">
              </div>
              <div
                class="absolute inset-0 bg-gradient-to-t from-black/80 via-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-all duration-300 flex flex-col justify-end p-6">
                <h3
                  class="text-lg font-bold transform translate-y-4 group-hover:translate-y-0 transition-transform duration-300">
                  {{ item.nama }}
                </h3>
                <p
                  class="text-sm text-gray-300 transform translate-y-4 group-hover:translate-y-0 transition-transform duration-300 delay-100">
                  {{ item.deskripsi }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>


      <section id="contact" class="py-20 bg-gray-900/50 slide-in">
  <div class="container mx-auto px-4 text-center">
    <h2 class="text-4xl font-bold mb-12 bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
      Contact
    </h2>
    <div class="flex flex-wrap justify-center gap-8">
      <a v-for="social in socials" :key="social.name" :href="social.link" class="flex flex-col items-center text-center w-full sm:w-1/2 md:w-1/3 lg:w-1/4 transform hover:scale-110 transition-transform duration-300">
        <img :src="social.image" alt="social.name" class="w-12 h-12 mx-auto">
        <p class="mt-2 text-sm font-medium text-secondary">{{ social.name }}</p>
      </a>
    </div>
  </div>
</section>
    </div>
  </div>
</template>

<script>
import appdev from '@/assets/app_developer.png'
import cplusplus from '@/assets/C++_Developer.png'
import coddev from '@/assets/Coding_Development_certificate-1.png'
import codefund from '@/assets/coding_fundamental.png'
import science from '@/assets/scientific_exploration.png'
import webdev from '@/assets/web_developer.png'
import vlog from '@/assets/vlog_kelas8.jpg'
import java from '@/assets/java_elite.jpeg'
import vue from '@/assets/logo.png'
import tailwind from '@/assets/tailwind.png'
import bootstrap from '@/assets/bootstrap.png'
import javalogo from '@/assets/java.png'
import python from '@/assets/python.jpeg'
import github from '@/assets/github.png'
import linkedin from '@/assets/linkedin.png'
import email from '@/assets/email.png'
import whatsapp from '@/assets/whatsapp.jpg'
import instagram from '@/assets/instagram.png'
export default {
  name: 'App',
  data() {
    return {
      menuItems: ['Biodata', 'Education', 'Tech Stack', 'Certificate', 'Contact'],
      techStacks: [
        { name: 'Vue.js', image: vue },
        { name: 'Tailwind CSS', image: tailwind },
        { name: 'Bootstrap', image: bootstrap },
        { name: 'Java', image: javalogo }
      ],
      education: [
        { year: '2024-2027', school: 'SMK Raden Umar Said Kudus', description: 'PPLG Major' },
        { year: '2021-2024', school: 'MTS Pembangunan Jakarta', description: 'Learn basic of programming' },
        { year: '2015-2021', school: 'MI Pembangunan Jakarta', description: 'Study academic lessons seriously' }
      ],
      socials: [
        { name: 'GitHub', image: github, link: 'https://github.com/Fiqqar' },
        { name: 'LinkedIn', image: linkedin, link: 'https://www.linkedin.com/in/zulfiqar-silmy-setiawan-604190324/' },
        { name: 'Gmail', image: email, link: 'mailto:fiqarsilmy@gmail.com' },
        { name: 'WhatsApp', image: whatsapp, link: 'https://wa.me/6287786688370' },
        { name: 'Instagram', image: instagram, link: 'https://www.instagram.com/fiqarsilmy/' }
      ],
      certificate: [
        { nama: 'App Developer Certificate', deskripsi: 'I Use Thunkable to learn how to make an mobile app', image: appdev },
        { nama: 'C++ Developer Certificate', deskripsi: 'I learned C++ basic logic', image: cplusplus },
        { nama: 'Coding Development Certificate', deskripsi: 'In Coding Development, I use Replit as the platform. In this chapter I learned how to make a website using HTML, CSS, and JavaScript', image: coddev },
        { nama: 'Coding Fundamental Certificate', deskripsi: 'I learned how to make a program in Scratch', image: codefund },
        { nama: 'Science Exploration Certificate', deskripsi: 'I learned Biology, Chemistry, and Physics using Scratch', image: science },
        { nama: 'Web Developer Certificate', deskripsi: 'I learned JavaScript OOP and Vue.js basic logic', image: webdev },
        { nama: 'Vlog Certificate', deskripsi: 'Certificate of Achievement for winning 1st place in the 8th grade parallel vlog competition', image: vlog },
        { nama: 'Java (Elite Honors) Certificate', deskripsi: 'In Java Elite Honors, I learned Java basic logic and OOP', image: java },
        { nama: 'Python - Beginner Level', deskripsi: 'I learned to make basic programs using python', image: python }
      ],
      isMenuOpen: false
    }
  },
  methods: {
    scrollToElement(id) {
      const element = document.getElementById(id)
      if (!element) return;
      const offset = 70
      const elementPosition = element.offsetTop - offset

      window.scrollTo({
        top: elementPosition,
        behavior: 'smooth'
      })
    },
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen
    }
  },
  mounted() {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('slide-in-show')
        }
      })
    }, { threshold: 0.1 })

    document.querySelectorAll('.slide-in').forEach(el => observer.observe(el))
  }
}
</script>

<style>
.slide-in {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.6s ease-out;
}

.slide-in-show {
  opacity: 1;
  transform: translateY(0);
}

html {
  scroll-padding-top: 70px;
}

.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 25px;
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 60;
}

.bar {
  height: 4px;
  background-color: white;
  transition: all 0.3s ease;
}

.hamburger.open .bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 7px);
}

.hamburger.open .bar:nth-child(2) {
  opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -7px);
}

.margin {
  margin-left: 0.100rem;
}
</style>
